	module.exports = {"data":{"markdownRemark":{"html":"<p>   The forest surrounding the Lake of Guldulin (\"The Lake of Dark Sorcery\")\nboasts a rich riparian flora and fauna. Although its wildlife may be of great\ninterest to the budding naturalist it is its legendary human residents which\nrouse the greatest curiosity. Evidence of humanoid activity comes in the form\nof wickermen, corn dollies and stickmen hung from oak and willow trees in the\nswamps surrounding the lake. The mutilated corpses of woodland animals have\nalso been hung from these trees. It is rumoured that the cultists of Guldulin\nmay be behind these strange events. This strange human sect was corrupted by a\nhenchman of Sauron, Angmacar the Black, in the latter part of the 3rd Age.\nUpon the death of Angmacar the sect vanished from the history books but it is\nrumoured they would return upon the rebirth of their leader.</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"The Legend of Lake Guldulin"}}},"pathContext":{"path":"/books/legendoflakeguldulin"}}

